// src/components/PlaceDetail.tsx
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Card, Accordion } from 'react-bootstrap';
import moment from 'moment';
import WeatherIcon from '../components/Weather/WeatherIcon';
import './PlaceDetail.css';

interface WeatherDataEntry {
  datetime: string;
  temperature_celsius?: number;
  relative_humidity_percent?: number;
  wind_speed_m_s?: number;
  wind_direction?: string;
  wind_beaufort_scale?: number;
  total_precipitation_mm?: number;
  storm_probability_percent?: number;
  total_cloud_cover_percent?: number;
  pressure_hPa?: number;
  [key: string]: any;
}

interface PlaceData {
  name: string;
  description: string | null;
  latitude: number;
  longitude: number;
  elevation: number;
  weather_data: WeatherDataEntry[];
}

const PlaceDetail: React.FC = () => {
  const {
    continentSlug,
    countrySlug,
    regionSlug,
    municipalitySlug,
    placeSlug,
  } = useParams<{
    continentSlug: string;
    countrySlug: string;
    regionSlug: string;
    municipalitySlug: string;
    placeSlug: string;
  }>();

  const [placeData, setPlaceData] = useState<PlaceData | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    // Updated API URL without continentSlug
    const apiUrl = `/api/${countrySlug}/${regionSlug}/${municipalitySlug}/${placeSlug}/`;

    fetch(apiUrl)
      .then((response) => {
        if (!response.ok) {
          throw new Error('Failed to fetch place details');
        }
        return response.json();
      })
      .then((data) => {
        setPlaceData(data);
        setLoading(false);
      })
      .catch((error) => {
        setError(error.message);
        setLoading(false);
      });
  }, [countrySlug, regionSlug, municipalitySlug, placeSlug]);

  if (loading) {
    return <div className="text-center my-4">Loading...</div>;
  }

  if (error) {
    return (
      <div className="alert alert-danger my-4" role="alert">
        {error}
      </div>
    );
  }

  if (!placeData) {
    return null;
  }

  // Process weather data to group by date
  const weatherDataByDate = placeData.weather_data.reduce(
    (acc: { [date: string]: WeatherDataEntry[] }, entry) => {
      const date = moment(entry.datetime).format('YYYY-MM-DD');
      if (!acc[date]) {
        acc[date] = [];
      }
      acc[date].push(entry);
      return acc;
    },
    {}
  );

  // Get daily summaries
  const dailySummaries = Object.keys(weatherDataByDate).map((date) => {
    const entries = weatherDataByDate[date];
    const temperatures = entries.map((e) => e.temperature_celsius || 0);
    const minTemp = Math.min(...temperatures);
    const maxTemp = Math.max(...temperatures);

    // Determine the most severe weather condition for the day
    const weatherIconState = determineDailyWeatherIcon(entries);

    return {
      date,
      minTemp,
      maxTemp,
      weatherIconState,
      entries,
    };
  });

  // Function to determine daily weather icon state based on weather data
  const determineDailyWeatherIcon = (entries: WeatherDataEntry[]): string => {
    // Priority order of weather conditions
    const conditionsPriority = [
      'thundershowersday',
      'thundershowersnight',
      'thunderrain',
      'thunder',
      'rainsnowshowersday',
      'rainsnowshowersnight',
      'rainsnow',
      'snowshowersday',
      'snowshowersnight',
      'snow',
      'sleet',
      'hail',
      'rain',
      'showersday',
      'showersnight',
      'fog',
      'wind',
      'cloudy',
      'partlycloudy',
      'partlycloudy-night',
      'clear-night',
      'sunny',
    ];

    for (const condition of conditionsPriority) {
      if (entries.some((entry) => getWeatherIconState(entry) === condition)) {
        return condition;
      }
    }
    return 'cloudy';
  };

  // Function to determine weather icon state based on individual weather data entry
  const getWeatherIconState = (entry: WeatherDataEntry): string => {
    const cloudCover = entry.total_cloud_cover_percent || 0;
    const precipitation = entry.total_precipitation_mm || 0;
    const stormProbability = entry.storm_probability_percent || 0;
    const temperature = entry.temperature_celsius || 0;

    const isDayTime = moment(entry.datetime).hour() >= 6 && moment(entry.datetime).hour() < 18;

    if (stormProbability >= 80) {
      if (isDayTime) return 'thundershowersday';
      else return 'thundershowersnight';
    } else if (precipitation > 0 && temperature <= 0) {
      if (isDayTime) return 'snowshowersday';
      else return 'snowshowersnight';
    } else if (precipitation > 0 && temperature > 0) {
      if (isDayTime) return 'showersday';
      else return 'showersnight';
    } else if (cloudCover > 75) {
      return 'cloudy';
    } else if (cloudCover > 25) {
      return isDayTime ? 'partlycloudy' : 'partlycloudy-night';
    } else {
      return isDayTime ? 'sunny' : 'clear-night';
    }
  };

  // Function to get wind icon class based on wind speed (using weather-icons)
  const getWindIconClass = (windSpeed: number | undefined) => {
    if (windSpeed === undefined) return '';
    const beaufortScale = Math.round(windSpeed / 1.944); // Convert m/s to knots and then to Beaufort scale approximation
    return `wi wi-wind-beaufort-${beaufortScale}`;
  };

  return (
    <div className="container place-detail-container">
      <Card className="my-4">
        <Card.Body>
          <Card.Title>{placeData.name}</Card.Title>
          {placeData.description && <Card.Text>{placeData.description}</Card.Text>}
          <Card.Text>
            <strong>Location:</strong> {placeData.latitude}, {placeData.longitude}
          </Card.Text>
          <Card.Text>
            <strong>Elevation:</strong> {placeData.elevation} meters
          </Card.Text>
        </Card.Body>
      </Card>

      {dailySummaries.length > 0 ? (
        <Accordion defaultActiveKey="0">
          {dailySummaries.map((dailySummary, index) => (
            <Accordion.Item eventKey={String(index)} key={index}>
              <Accordion.Header>
                <div className="d-flex justify-content-between align-items-center w-100">
                  <div>
                    <strong>{moment(dailySummary.date).format('dddd, MMMM Do')}</strong>
                    <div>
                      {dailySummary.minTemp}C / {dailySummary.maxTemp}C
                    </div>
                  </div>
                  <WeatherIcon
                    state={dailySummary.weatherIconState}
                    width={64}
                    height={64}
                    color="#000"
                  />
                </div>
              </Accordion.Header>
              <Accordion.Body>
                <div className="row">
                  {dailySummary.entries.map((entry, idx) => (
                    <div key={idx} className="col-12 col-md-6 col-lg-4 mb-3">
                      <Card className="hourly-card">
                        <Card.Body className="d-flex align-items-center">
                          <div className="flex-shrink-0">
                            <WeatherIcon
                              state={getWeatherIconState(entry)}
                              width={48}
                              height={48}
                              color="#000"
                            />
                          </div>
                          <div className="flex-grow-1 ms-3">
                            <Card.Title>
                              {moment(entry.datetime).format('HH:mm')}
                            </Card.Title>
                            <Card.Text>
                              <strong>Temp:</strong> {entry.temperature_celsius}C
                            </Card.Text>
                            <Card.Text>
                              <strong>Wind:</strong> {entry.wind_speed_m_s} m/s{' '}
                              {entry.wind_direction}{' '}
                              <i
                                className={`wi ${getWindIconClass(entry.wind_speed_m_s)}`}
                              ></i>
                            </Card.Text>
                            <Card.Text>
                              <strong>Humidity:</strong> {entry.relative_humidity_percent}%
                            </Card.Text>
                          </div>
                        </Card.Body>
                      </Card>
                    </div>
                  ))}
                </div>
              </Accordion.Body>
            </Accordion.Item>
          ))}
        </Accordion>
      ) : (
        <div className="alert alert-info mt-4" role="alert">
          No weather data available.
        </div>
      )}
    </div>
  );
};

export default PlaceDetail;
